html {
    font-family: "Fira Mono", monospace;
    font-size: 100%;
    -webkit-font-smoothing: antialiased;
    background: #282c34;
    color: #d7dae0;
}
/* Copy & Lists */
p {
    line-height: 2rem;
    margin-top: 1.5rem;
    margin-bottom: 0;
}
ul {
    padding: 0;
    list-style: none;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    li {
        line-height: 1.5rem;
        display: inline-block;
        margin-right: 10px;
    }
}

/* Headings */
h1,
h2 {
    margin-top: 1.5rem;
    margin-bottom: 0;
    line-height: 1.5rem;
    color: #ffffff;
}
h1 {
    font-size: 4rem;
    line-height: 4.5rem;
    margin-top: 3rem;
}
h2 {
    font-size: 2.828rem;
    line-height: 3rem;
    margin-top: 3rem;
}
/* Tables */
/* Code blocks */
/* Leading paragraph text */
.lead {
    font-size: 1.5rem;
}

main {
    display: block;
    width: 900px;
    margin: 0 auto;
}

a {
    color: #e5c07b;
    text-decoration: none;
    border-bottom: none;
    &:hover,
    &:focus {
        border-bottom: 1px solid $green;
    }
}

@media only screen and (max-width: 812px) {
    body {
        margin: 16px;
    }

    main {
        width: 100%;
    }
}
